import Header from '@/components/Header.vue'
import { Component, Vue } from "vue-property-decorator";

@Component({
  components: {
    Header,
  }
})
export default class AllBetRecord extends Vue {
  private currGameTitle?: any = '全部';
  private currDateTitle?: any = '今日';
  private activeGameTitle?: number = 0;
  private activeDateTitle?: number = 0;
  private loading?: boolean = false;
  private finished?: boolean = false;
  private showSelectDate?: boolean = false;
  private currentDate?: any = new Date();
  private minDate?: any = new Date(1970, 0, 1);
  private maxDate?: any = new Date();
  private gameTitleItems?: any = ['全部', 'AA彩票'];
  private dateTitleItems?: any = [
    '今日',
    '昨日',
    '近7日',
    '近30日',
    '自定义'
  ];
  private betRecordData?: any = [
    {
      date: ['01','29'],
      entries: '24',
      turnOver: '1126',
      winLose: '-5020.40',
      gameType: 'AA彩票',
      gameName: '幸运飞艇',
      odds: '',
      totalTurnOver: '500',
      totalWinLose: '-500',
      betTime: '2020-01-29 19:30:45',
    },
    {
      date: ['01','29'],
      entries: '24',
      turnOver: '1126',
      winLose: '-5020.40',
      gameType: 'AA彩票',
      gameName: '幸运飞艇',
      odds: '',
      totalTurnOver: '500',
      totalWinLose: '-500',
      betTime: '2020-01-29 19:30:45',
    },
    {
      date: ['01','29'],
      entries: '24',
      turnOver: '1126',
      winLose: '-5020.40',
      gameType: 'AA彩票',
      gameName: '幸运飞艇',
      odds: '',
      totalTurnOver: '500',
      totalWinLose: '-500',
      betTime: '2020-01-29 19:30:45',
    },
  ];
  private onLoad(): void {
    setTimeout(() => {
      this.loading = false;
      this.finished = true;
    }, 1000);
  }
  private onRefresh(): void {
    this.finished = false;
    this.loading = true;
    this.onLoad();
  }
  private selectGameTitle(index: number, title: string) {
    this.activeGameTitle = index;
    this.currGameTitle = title;
    let _gametitle = this.$refs.gametitle as any;
    _gametitle.toggle();
  }
  private selectDateTitle(index: number, title: string) {
    let _datetitle = this.$refs.datetitle as any;
    _datetitle.toggle();

    if (title == '自定义') {
      this.showSelectDate = true;
    } else {
      this.activeDateTitle = index;
      this.currDateTitle = title;
    }
  }

  private selectDate(val: any) {
    this.showSelectDate = false;
    console.log(val);
  }
}
